/*  //////////////////////////////////////////SERVICIOS////////////////////////////////////////////////////// */

.marquee-wrap {
  position: relative;
  display: block;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
}

.marquee-wrap,
.marquee-wrap ul {
  margin-left: 0;
  padding-left: 0;
  list-style-type: none;
}

.marquee-wrap > li {
  display: flex;
  align-items: center;
  width: 150vw;
  width: var(--scroller-size);
  will-change: transform;
  -webkit-animation: text-scroller 7500ms linear 0ms normal infinite;
          animation: text-scroller 7500ms linear 0ms normal infinite;
  text-indent: 0;
  justify-content: space-around;
  transition: all 3000ms linear 0ms;
  white-space: nowrap;
  font-family: Agrandir-WideLight;
  color: lawngreen;
}

@-webkit-keyframes text-scroller {
  100% {
    transform: translateX(calc(150vw * -0.5));
    transform: translateX(calc(var(--scroller-size) * -0.5));
  }
}

@keyframes text-scroller {
  100% {
    transform: translateX(calc(150vw * -0.5));
    transform: translateX(calc(var(--scroller-size) * -0.5));
  }
}

.border {
  border-top: 2px solid lawngreen;
}

.marquee-wrap > li:nth-child(2) {
  animation-direction: reverse;
}

.marquee-wrap > li:nth-child(4) {
  animation-direction: reverse;
}

.marquee-wrap > li:nth-child(6) {
  animation-direction: reverse;
  border-bottom: 2px solid lawngreen;
}

.marquee-wrap > li > ul li {
  display: inline-block;
  padding: 0.5vh 4vw;
  font-size: 11.6vh;
  text-transform: uppercase;
}

:root {
  --scroller-size: 150vw;
}

@media only screen and (max-width: 600px) {
  :root {
    --scroller-size: 200vw;
  }
}

